#modal-feedback .button {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  display: block;
  position: relative;
  width: 300px;
  height: 78px;
  transition: transform 0.3s ease-in;
  border-radius: 40px;
}
@media (max-width: 992px) {
  #modal-feedback .button {
    width: 230px;
    height: 65px;
  }
}
@media (max-width: 576px) {
  #modal-feedback .button {
    width: 150px;
    height: 40px;
  }
}
#modal-feedback .button .button-background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
  background-color: #468217;
  transform-origin: center;
  transition: all 0.2s ease;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0);
  z-index: 2;
  border-radius: 40px;
  overflow: hidden;
}
#modal-feedback .button .button-background.button-animation::before {
  content: "";
  opacity: 0.7;
  position: absolute;
  top: 0;
  transform: translateX(-125%);
  width: 100%;
  height: 100%;
  background: linear-gradient(100deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 25%, hsla(0, 0%, 84%, 0.25) 51%, rgba(0, 0, 0, 0) 76%, rgba(0, 0, 0, 0));
  filter: blur(2px);
  transition: opacity 1s ease;
  animation: sheen 3s forwards infinite;
}
#modal-feedback .button .button-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 100%;
  color: #ffffff;
  z-index: 3;
  position: relative;
}
@media (max-width: 992px) {
  #modal-feedback .button .button-text {
    font-size: 18px;
  }
}
@media (max-width: 576px) {
  #modal-feedback .button .button-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
  }
}
#modal-feedback .button:not(:disabled):hover .button-background, #modal-feedback .button:not(:disabled):focus .button-background {
  transition: all 0.2s ease-in 0.1s;
  background-color: #417915;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0.39);
}
#modal-feedback .button:not(:disabled):hover .button-background::before, #modal-feedback .button:not(:disabled):focus .button-background::before {
  opacity: 0;
}
#modal-feedback .button:not(:disabled):hover .button-background {
  transform: scale(0.96);
}
#modal-feedback .button:not(:disabled):active {
  transform: translateY(3px);
}
@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#modal-feedback .button:disabled .button-background {
  background-color: rgba(95, 95, 95, 0.8784313725);
}

#modal-feedback {
  background: #ffffff;
  border-radius: 20px;
  display: none;
}
@media (max-width: 576px) {
  #modal-feedback {
    border-radius: 16px;
  }
}
#modal-feedback .is-close {
  opacity: 0.5 !important;
  transition: all 0.4s !important;
  top: 5px !important;
  right: 10px !important;
}
@media (max-width: 576px) {
  #modal-feedback .is-close {
    top: 0px !important;
    right: 0px !important;
  }
}
#modal-feedback .is-close:hover {
  opacity: 0.8 !important;
}
#modal-feedback h3 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  line-height: 115%;
  color: #2b2b2b;
  text-align: center;
  font-size: calc(0.011875 * 100vw + 22.2px);
}
#modal-feedback h3 br {
  display: none;
}
@media (min-width: 1921px) {
  #modal-feedback h3 {
    font-size: 45px;
  }
}
@media (max-width: 320px) {
  #modal-feedback h3 {
    font-size: 26px;
  }
}
@media (max-width: 576px) {
  #modal-feedback h3 br {
    display: block;
  }
}

#modal-feedback {
  max-width: 860px;
  width: 100%;
  padding: 46px 30px 26px;
  margin: 0 auto;
}
#modal-feedback .vita-input--error::placeholder {
  color: rgba(214, 85, 85, 0.7);
  opacity: 1;
}
@media (max-width: 576px) {
  #modal-feedback {
    padding: 30px 11px 14px;
    border-radius: 16px;
  }
}
#modal-feedback h3 {
  margin-bottom: 40px;
}
@media (max-width: 992px) {
  #modal-feedback h3 {
    margin-bottom: 25px;
  }
}
#modal-feedback .button {
  margin: 42px auto 30px;
}
@media (max-width: 576px) {
  #modal-feedback .button {
    margin-top: 34px;
    margin-bottom: 17px;
    width: 145px;
  }
}
#modal-feedback .decr {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: #707070;
  max-width: 600px;
  margin: 0 auto;
}
@media (max-width: 576px) {
  #modal-feedback .decr {
    font-size: 12px;
    line-height: 19px;
  }
}
#modal-feedback .inputs-container {
  max-width: 575px;
  margin: 0 auto;
  display: flex;
  gap: 14px;
  flex-direction: column;
}
@media (max-width: 576px) {
  #modal-feedback .inputs-container {
    gap: 10px;
    max-width: 330px;
  }
}