
@import "../../scss/_global.scss";

#modal-feedback {
  .vita-input--error {
    &::placeholder {
      color: rgb(214 85 85 / 70%);
      opacity: 1;
    }
  }

  @extend %modal;

  max-width: 860px;
  width: 100%;
  padding: 46px 30px 26px;
  margin: 0 auto;
  @media (max-width: 576px) {
    padding: 30px 11px 14px;
    border-radius: 16px;
  }

  h3 {
    margin-bottom: 40px;

    @media (max-width: 992px) {
      margin-bottom: 25px;
    }
  }

  .button {
    @extend %button;
    margin: 42px auto 30px;

    @media (max-width: 576px) {
      margin-top: 34px;
      margin-bottom: 17px;
      width: 145px;
    }
  }

  .decr {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #707070;
    max-width: 600px;
    margin: 0 auto;

    @media (max-width: 576px) {
      font-size: 12px;
      line-height: 19px;
    }
  }

  .inputs-container {
    max-width: 575px;
    margin: 0 auto;

    display: flex;
    gap: 14px;
    flex-direction: column;

    @media (max-width: 576px) {
      gap: 10px;
      max-width: 330px;
    }
  }
}
