/* Upload Area */
.upload-area {
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border-radius: 10px;
  min-height: 122px;
  height: auto;
  display: flex;
  padding: 16px 23px;
  border: 2px solid #cecece;
  position: relative;
  transition: all 0.3s ease;
}
@media (max-width: 576px) {
  .upload-area {
    padding: 12px 14px;
    border-radius: 6px;
  }
}
.upload-area:hover, .upload-area:focus {
  transform: translateY(-1px);
  border: 2px solid rgb(86, 77, 77);
  box-shadow: 0 0 7px 1px rgba(86, 77, 77, 0.45);
}
.upload-area_enabel {
  cursor: pointer;
}
.upload-area .drop-zoon__file-input {
  position: absolute;
  width: 100%;
  cursor: pointer;
  height: 100%;
  opacity: 0;
}
.upload-area .drop-zoon__text-wrap {
  padding-bottom: 5px;
  transition: 0.3s all;
}
.upload-area .drop-zoon__paragraph {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 31px;
  text-align: center;
  color: #2b2b2b;
  margin-bottom: 20px;
}
@media (max-width: 576px) {
  .upload-area .drop-zoon__paragraph {
    font-size: 14px;
    line-height: 16px;
  }
}
.upload-area .drop-zoon__comment {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: #707070;
}
@media (max-width: 576px) {
  .upload-area .drop-zoon__comment {
    font-size: 12px;
    line-height: 14px;
  }
}
.upload-area .dropZoon__img-container {
  height: auto;
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: stretch;
  margin-top: 5px;
  row-gap: 10px;
  transition: 0.3s all;
}
@media (max-width: 767px) {
  .upload-area .dropZoon__img-container {
    flex-wrap: wrap;
  }
}
.upload-area .dropZoon__img-item {
  position: relative;
  display: flex;
  align-items: center;
  width: 20%;
  height: 100px;
  max-height: 200px;
  border: 5px solid #fbfbfb;
  background: rgba(0, 0, 0, 0.1);
}
.upload-area .dropZoon__img-item img {
  width: 100%;
  height: auto;
  max-height: 100px;
  object-fit: cover;
}
.upload-area .dropZoon__img-item button {
  position: absolute;
  top: 5px;
  right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  background-color: #bbbbbb;
  color: #1e1e1e;
  opacity: 0.7;
  font-size: 14px;
  line-height: 1;
  border-radius: 3px;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  transition: 0.3s all;
}
.upload-area .dropZoon__img-item button:hover {
  opacity: 1;
}
.upload-area .drop-text-enter-active,
.upload-area .drop-text-leave-active,
.upload-area .drop-img-enter-active,
.upload-area .drop-img-leave-active {
  overflow: hidden;
  opacity: 0;
}
.upload-area .drop-text-enter,
.upload-area .drop-text-leave-to,
.upload-area .drop-img-enter,
.upload-area .drop-img-leave-to {
  height: 0;
}

/* Upload Area */