
/* Upload Area */
.upload-area {
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  border-radius: 10px;
  min-height: 122px;
  height: auto;
  display: flex;
  padding: 16px 23px;
  border: 2px solid #cecece;
  position: relative;
  transition: all 0.3s ease;

  @media (max-width: 576px) {
    padding: 12px 14px;
    border-radius: 6px;
  }

  &:hover,
  &:focus {
    transform: translateY(-1px);
    border: 2px solid rgb(86, 77, 77);
    box-shadow: 0 0 7px 1px rgb(86 77 77 / 45%);
  }

  &_enabel {
    cursor: pointer;
  }

  .drop-zoon__file-input {
    position: absolute;
    width: 100%;
    cursor: pointer;
    height: 100%;
    opacity: 0;
  }

  .drop-zoon__text-wrap {
    padding-bottom: 5px;
    transition: 0.3s all;
  }

  .drop-zoon__paragraph {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 31px;
    text-align: center;
    color: #2b2b2b;
    margin-bottom: 20px;

    @media (max-width: 576px) {
      font-size: 14px;
      line-height: 16px;
    }
  }

  .drop-zoon__comment {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #707070;

    @media (max-width: 576px) {
      font-size: 12px;
      line-height: 14px;
    }
  }

  .dropZoon__img-container {
    height: auto;
    width: 100%;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: stretch;
    margin-top: 5px;
    row-gap: 10px;
    transition: 0.3s all;

    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }

  .dropZoon__img-item {
    position: relative;
    display: flex;
    align-items: center;
    width: 20%;
    height: 100px;
    max-height: 200px;
    border: 5px solid #fbfbfb;
    background: rgba(0, 0, 0, 0.1);

    img {
      width: 100%;
      height: auto;
      max-height: 100px;
      object-fit: cover;
    }

    button {
      position: absolute;
      top: 5px;
      right: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 15px;
      height: 15px;
      background-color: #bbbbbb;
      color: #1e1e1e;
      opacity: 0.7;
      font-size: 14px;
      line-height: 1;
      border-radius: 3px;
      border: none;
      outline: none;
      box-shadow: none;
      cursor: pointer;
      transition: 0.3s all;

      &:hover {
        opacity: 1;
      }
    }
  }

  .drop-text-enter-active,
  .drop-text-leave-active,
  .drop-img-enter-active,
  .drop-img-leave-active {
    overflow: hidden;
    opacity: 0;
  }

  .drop-text-enter,
  .drop-text-leave-to,
  .drop-img-enter,
  .drop-img-leave-to {
    height: 0;
  }
}
/* Upload Area */
